import React ,{useState} from 'react';
import './Header.css';


const Header = () => {

const [clicked,setClicked]=useState(false);


const handleClick=()=>{
    setClicked(!clicked);
};

    return (
        <div className="header">
            <div className="nav">
            <div className="logo">
                <h3><span>Fur</span>niture  <i className="fas fa-couch"></i></h3>
            </div>
            <div className={ clicked ? "menu" : "menu close"}>
                <a href="#">Home</a>
                <a href="#about">About</a>
                <a href="#feature">Feature</a>
                <a href="#products">Products</a>
                <a href="#contact">Contact</a>
              
            </div>
            <div className="icon" onClick={handleClick}>
            <i className={clicked ? "fas fa-times": "fas fa-bars"}></i>
            <i class="fas fa-chair"></i>
        
            </div>
            </div>
            <svg xmlns="http://www.w3.org/2000/svg" className="svg" viewBox="0 0 1440 320"><path fill="#000" fill-opacity="1" d="M0,160L288,64L576,192L864,160L1152,160L1440,224L1440,0L1152,0L864,0L576,0L288,0L0,0Z"></path></svg>
        </div>
    )
}

export default Header;
